[data-theme='1'] {
    --color: #000000;
    --background: #FFFFFF;
}

[data-theme='2'] {
    --color: #000000;
    --background: #DDEEFF;
}

[data-theme='3'] {
    --color: #FFFFFF;
    --background: #212A34;
}

html {
    background-color: var(--background);
    color: var(--color);
}

.select {
    width: 172px;
    /*height: 36px;*/
    padding: 10px;
    border-radius: 2px;
    border: 1px solid var(--color);
    color: var(--color);
    background-color: var(--background);
}
